*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  color: #222222;
  width: 100%;
  margin: 0;
  font-size: 1.6rem;
}

h1,
h2,
h3,
h4 {
  font-weight: 700;
}

h5,
h6,
p {
  font-weight: 400;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: bottom;
}

a {
  text-decoration: none;
  font-size: 1.6rem;
  display: inline-block;
}

ul {
  list-style: none;
  padding-inline: 0;
  margin-block: 0;
  font-size: 1.6rem;
}

th,
td {
  padding: 0;
  font-size: 1.6rem;
}

textarea {
  display: block;
  font-size: 1.6rem;
}

tbody {
  width: 100%;
}

.button {
  font-size: 1.4rem;
  color: #fff;
  border-radius: 0;
  cursor: pointer;
}

.gnav__button {
  height: 4.2rem;
  width: 12.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button--black {
  background-color: #222222;
  font-weight: 700;
}

.button--gold {
  background-color: #afa86f;
  font-family: Viga, "Noto Sans JP", sans-serif;
}

.footer {
  width: 100%;
}

.footer__inner {
  width: 100%;
  padding-inline: 2rem;
  max-width: 1440px;
  padding-block: 49px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 1439px) {
  .footer__inner {
    padding-inline: 1.8rem;
  }
}
@media screen and (max-width: 767px) {
  .footer__inner {
    padding-inline: 1.6rem;
    flex-direction: column;
    row-gap: 3.2rem;
  }
}

.copy-right {
  font-size: 1rem;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  background-color: #faf7f1;
}

.sub-page-header {
  position: static;
  background-color: #fff;
}

.header__inner {
  margin: 0 auto;
  width: min(100%, 1440px);
  padding: 2.6rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 1439px) {
  .header__inner {
    padding: 1.6rem 2rem;
  }
}
@media screen and (max-width: 767px) {
  .header__inner {
    padding: 0.8rem 1.2rem;
  }
}

.header--sticky {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: 100;
  visibility: visible;
  width: 100%;
  animation: slideDown 0.3s ease-in-out;
  background-color: #fff;
}

@keyframes slideDown {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
  }
}
@media screen and (max-width: 1439px) {
  .header__logo {
    width: 20rem;
  }
}
@media screen and (max-width: 767px) {
  .header__logo {
    width: 15.1rem;
  }
}

.gnav__list {
  display: flex;
  align-items: center;
}

.gnav__item {
  text-align: center;
}

.gnav__item:first-of-type {
  margin-right: 2.2rem;
}

.gnav__item:nth-of-type(2) {
  margin-right: 4rem;
}

@media screen and (max-width: 767px) {
  .gnav__button {
    display: none;
  }
}

.burger-button {
  width: 5.2rem;
  height: 5.2rem;
  cursor: pointer;
  z-index: 1000;
}
@media screen and (max-width: 1439px) {
  .burger-button {
    width: 4rem;
  }
}
@media screen and (max-width: 767px) {
  .burger-button {
    width: 3.2rem;
  }
}

.burger-button__icon {
  display: block;
  position: relative;
  width: 100%;
  height: 3rem;
}

.burger-button__line {
  display: inline-block;
  position: absolute;
  left: 0;
  width: 100%;
  background: #fff;
  height: 0.2rem;
  transition: all 0.4s;
}

.burger-button__line:nth-of-type(1) {
  top: 0;
  transform: translateY(-50%);
}

.burger-button__line:nth-of-type(2) {
  top: 1.5rem;
  transform: translateY(-50%);
}

.burger-button__line:nth-of-type(3) {
  top: 3rem;
  transform: translateY(-50%);
}

.burger-button__label {
  display: inline-block;
  font-family: Viga, "Noto Sans JP", sans-serif;
  font-size: 1.2rem;
  margin-top: 1rem;
  color: #fff;
  transition: all 0.4s;
}
@media screen and (max-width: 1439px) {
  .burger-button__label {
    margin-top: 0.8rem;
    font-size: 1.1rem;
  }
}
@media screen and (max-width: 767px) {
  .burger-button__label {
    margin-top: 0.6rem;
    font-size: 1rem;
  }
}

.burger-button.open .burger-button__line:nth-of-type(1) {
  top: 50%;
  transform: rotate(-45deg);
}

.burger-button.open .burger-button__line:nth-of-type(2) {
  opacity: 0;
}

.burger-button.open .burger-button__line:nth-of-type(3) {
  top: 50%;
  transform: rotate(45deg);
}

.burger-button.open .burger-button__label {
  visibility: hidden;
}

.burger-menu {
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.9);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s linear;
  text-align: center;
}

.burger-menu.open {
  pointer-events: auto;
  opacity: 1;
}

.burger-menu__list {
  margin-top: 20%;
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
}

.burger-menu__item {
  font-size: 1.3rem;
}

.burger-menu__anchor {
  display: inline-block;
}

.burger-menu .button {
  width: 20rem;
  line-height: 6.9rem;
  border-radius: 6rem;
}

body {
  background-color: #fff;
}

.bold {
  font-weight: 700;
}

.sp-ms-break {
  display: none;
}
@media screen and (max-width: 374px) {
  .sp-ms-break {
    display: block;
  }
}

.sp-break {
  display: none;
}
@media screen and (max-width: 767px) {
  .sp-break {
    display: block;
  }
}

.tab-break {
  display: none;
}
@media screen and (max-width: 1439px) {
  .tab-break {
    display: block;
  }
}

.only-ipad-break {
  display: none;
}
@media screen and (max-width: 1023px) {
  .only-ipad-break {
    display: block;
  }
}
@media screen and (max-width: 767px) {
  .only-ipad-break {
    display: none;
  }
}

.only-tab-break {
  display: none;
}
@media screen and (max-width: 1439px) {
  .only-tab-break {
    display: block;
  }
}
@media screen and (max-width: 767px) {
  .only-tab-break {
    display: none;
  }
}

.pc-and-tab-break {
  display: block;
}
@media screen and (max-width: 767px) {
  .pc-and-tab-break {
    display: none;
  }
}

.pc-break {
  display: block;
}
@media screen and (max-width: 1439px) {
  .pc-break {
    display: none;
  }
}

.sp-text-spacing {
  display: none;
}
@media screen and (max-width: 767px) {
  .sp-text-spacing {
    display: block;
    margin-top: 1.8rem;
  }
}

.text-inline-blocked {
  display: inline-block;
}

.main__inner {
  margin-inline: auto;
}

.table-heading {
  vertical-align: middle;
}

.table-heading--vertical-top {
  margin-block: 0;
}